<template>
  <div id="order-received">
    <order-nav></order-nav>
    <h3>已接工单</h3>
    <!-- 已接工单汇总 -->
    <div class="order_table">
      <el-table
        :data="orderGatherList"
        border
        style="width: 90%;margin:0 auto"
        max-height="500"
        size="small"
      >
        <el-table-column type="index" label="序号" width="60"></el-table-column>
        <el-table-column prop="initiate_person" label="发单人"></el-table-column>
        <el-table-column prop="initiate_time" label="发单时间"></el-table-column>
        <el-table-column prop="out_time" label="结单时间"></el-table-column>
        <el-table-column prop="need" label="内容需求"></el-table-column>
        <el-table-column prop="next_info" label="跟进信息"></el-table-column>
        <el-table-column prop="job_type" label="工单类型"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
        <el-table-column label="结单" width="180">
          <template slot-scope="{row}">
            <div v-if="row.status == '进行中'">
              <el-button type="primary" size="mini" @click="jiedan(row.id)">结单</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 按钮部分 -->
      <!-- <div class="operate">
        <el-button type="primary" size="mini" @click="goTop">置顶</el-button>
      </div>-->
    </div>
  </div>
</template>
<script>
import orderNav from "@/common/order_nav";
export default {
  name: "",
  props: {},
  components: {
    orderNav
  },
  data() {
    return {
      // 查询相关数据
      queryInfo: {
        form: {
          // 选中的发单部门
          checkedDpm: "",
          // 选中的发单人
          checkedStaff: "",
          // 选中的工单类型
          checkedType: "",
          // 选中的截单时间
          checkedDate: "",
          // 页数
          page: 1
        },
        // 部门列表
        departmentList: [],
        // 接单人列表
        staff: [],
        // 工单类型列表
        type: ["及时", "远期"]
      },
      // 已接工单汇总列表
      orderGatherList: [],
      // 选中的多选框列表是否被选中
      checkedList: [],
      // 选中的多选框下标列表
      indexList: [],
      // 结单状态
      status: 0
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取已接工单汇总列表
    getAcceptedOrderList() {
      const userName = this.$store.state.userName;
      this.$api.queryAccepted(userName).then(res => {
        console.log(res);
        this.orderGatherList = res.data.data;
      });
    },
    // 置顶按钮
    goTop() {
      // 判断是否勾选
      let bool = this.checkedList.find(n => {
        return n == true;
      });
      if (!bool) {
        this.$message.error("请勾选工单");
        return;
      }
      // 获取那些索引被选中的列表
      this.indexList = []; //每次点击都得重置
      this.checkedList.forEach((n, index) => {
        if (n == true) {
          this.indexList.push(index);
        }
      });
      for (let i in this.indexList) {
        let top = this.orderGatherList[this.indexList[i]];
        this.orderGatherList.splice(this.indexList[i], 1);
        this.orderGatherList.unshift(top);
      }
      // 响应式更新数组
      for (let i in this.checkedList) {
        this.$set(this.checkedList, i, false);
      }
    },
    // 结单
    jiedan(id) {
      console.log(id);
      this.$confirm("确定结单吗？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.jiedan(id).then(res => {
            console.log(res);
            this.$message.success("结单成功");
            this.getAcceptedOrderList();
          });
        })
        .catch(() => {});
    },
    // 获取下拉框数据
    getSelectData() {
      this.$api.getSelectData().then(res => {
        console.log(res);
        this.queryInfo.departmentList = res.data.department_list;
      });
    },
    // 部门选择触发 提供相应员工
    departmentChange(val) {
      // bug：这样的话部门点击了，另一个地方的部门也能获取到员工信息
      this.$api.getSelectData(val).then(res => {
        console.log(res);
        this.queryInfo.staff = res.data.person_list;
      });
    },
    // 查询
    queryOrder() {
      this.$api.queryAccepted(this.queryInfo.form).then(res => {
        console.log(res);
        this.orderGatherList = res.data.data;
      });
    }
  },
  created() {
    this.getAcceptedOrderList();
    // this.getSelectData();
  },
  mounted() {}
};
</script>
<style>
#order-received .order_query .form_query .el-form-item__label {
  color: #fff;
}
#order-received .el-date-picker {
  width: 256px;
}
</style>
<style scoped>
#order-received {
  height: 92vh;
  background: rgba(255, 255, 255, 0.7);
}
.order_query {
  display: flex;
  justify-content: space-around;
  padding-top: 10px;
  /* background-color: #409eff; */
}
.form_query {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.order_table {
  margin-top: 24px;
  position: relative;
}
.operate {
  position: absolute;
  width: 200px;
  top: 6px;
  left: 170px;
}
thead {
  display: table-header-group;
}
tbody {
  display: table-row-group;
}
h3 {
  text-align: center;
}
</style>


